<template>
  <div class="home">
    <el-container class="home-container">
      <!-- 头部区域 -->
      <el-header>
        <div>
          <img src="~assets/img/admin-logo.jpg" alt="" class="admin-logo" />
          <span> 电商后台管理系统</span>
        </div>
        <el-button type="info" @click="logout">退出</el-button>
      </el-header>
      <!-- 页面主题区域 -->
      <el-container>
        <!-- 侧边栏 -->
        <el-aside :width="isCollapse? '64px':'200px'" >
          <div class="toggle-btn" @click="toggleCollapse()">|||</div>
          <!-- 侧边栏菜单效果 -->
          <el-menu
            background-color="#333744"
            text-color="#fff"
            active-text-color="#409bff"
            :unique-opened="true"
            :collapse="isCollapse"
            :collapse-transition="false"
            router
            :default-active="isActive"
          >
          <!-- 一级菜单 -->
            <el-submenu :index="item.id + ''" v-for="item in menulist" :key="item.id" >
              <!-- 一级菜单的模板区 -->
              <template slot="title">
                <i :class="iconsObj[item.id]" style="margin-right:5px"></i>
                <span>{{ item.authName}}</span>
              </template>
              <!-- 二级菜单项 -->
              <el-menu-item 
              :index="'/'+subItem.path" 
              v-for="subItem in item.children"
              :key="subItem.id"
              @click="saveNavClick('/'+subItem.path)"
              >
                <template slot="title">
                  <i class="el-icon-menu"></i>
                  <span>{{ subItem.authName }}</span>
                </template>
              </el-menu-item>
            </el-submenu>
          </el-menu>
        </el-aside>
        <!-- 右侧内容主题 -->
        <el-main>
          <!-- 路由占位符 -->
          <router-view />
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      // 左侧菜单数据
      menulist: [],
      iconsObj: {
        '125': 'iconfont icon-user',
        '103': 'iconfont icon-tijikongjian',
        '101': 'iconfont icon-shangpin',
        '102': 'iconfont icon-danju',
        '145': 'iconfont icon-baobiao'
      },
      // 是否折叠
      isCollapse: false,
      // 是否激活
      isActive: ''
    }
  },
  created() {
    this.getMenuList();
    // console.log(this.$route);
    this.isActive = window.sessionStorage.getItem('activePath')
  },
  methods: {
    logout() {
      window.sessionStorage.clear();
      this.$router.push("/login");
    },
    // 获取所有菜单
    async getMenuList() {
      const {data: res}  = await this.$http.get('/menus')
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.menulist = res.data;
      // console.log(res);
    },
    // 点击按钮 切换折叠或展开
    toggleCollapse() {
      this.isCollapse = !this.isCollapse;
    },
    // 保存按钮的激活状态
    saveNavClick(path) {
      window.sessionStorage.setItem('activePath', path)
      this.isActive = path
   
    }
    
  },
};
</script>

<style scoped>
.home-container {
  height: 100vh;
}
.admin-logo {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.el-header {
  background-color: #373d41;
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  align-items: center;
  color: #fff;
  font-size: 20px;
}

.el-header > div {
  display: flex;
  justify-content: center;
  align-items: center;
}
.el-header > div > span {
  margin-left: 15px;
}
.el-aside {
  background-color: #333744;
  
}
.el-aside > .el-menu {
  border: none;
}
.el-main {
  background-color: #eaedf1;
}
.toggle-btn {
  background-color: #4a5064;
  color: #fff;
  line-height: 24px;
  font-size: 10px;
  text-align: center;
  letter-spacing: 0.2em;
  cursor: pointer;
}
</style>